import { createRouter, createWebHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'

// 
const routes: RouteRecordRaw[] = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/HomePage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('@/views/AboutPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/members',
    name: 'Members',
    component: () => import('@/views/MembersPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/projects',
    name: 'Projects',
    component: () => import('@/views/ProjectsPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/blog',
    name: 'Blog',
    component: () => import('@/views/BlogPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/events',
    name: 'Events',
    component: () => import('@/views/EventsPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/meetings',
    name: 'Meetings',
    component: () => import('@/views/MeetingsPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import('@/views/ContactPage.vue'),
    meta: {
      title: '',
      description: ''
    }
  },
  // 
  {
    path: '/admin',
    name: 'Admin',
    component: () => import('@/components/admin/AdminLayout.vue'),
    meta: {
      title: '',
      description: '',
      requiresAdmin: true
    },
    children: [
      {
        path: '',
        name: 'AdminDashboard',
        component: () => import('@/components/admin/AdminMeetingManager.vue'),
        meta: { title: '' }
      },
      {
        path: 'members',
        name: 'AdminMembers',
        component: () => import('@/components/admin/AdminMemberManager.vue'),
        meta: { title: '' }
      }
    ]
  },
  // 404 
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('@/views/NotFoundPage.vue'),
    meta: {
      title: ''
    }
  }
]

// 
const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes,
  scrollBehavior(to, _from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else if (to.hash) {
      return { el: to.hash, behavior: 'smooth' }
    } else {
      return { top: 0 }
    }
  }
})

// 
router.beforeEach((to, _from, next) => {
  // 
  if (to.meta?.title) {
    document.title = `${to.meta.title} - DUT`
  }
  
  // 
  if (to.meta?.requiresAdmin) {
    // 
    const isAdmin = localStorage.getItem('adminToken')
    if (!isAdmin) {
      console.log(' ...')
      
      // 
      window.dispatchEvent(new CustomEvent('showAdminLogin', {
        detail: { redirectTo: to.fullPath }
      }))
      
      // 
      return
    }
  }
  
  next()
})

// 
router.afterEach((to, _from) => {
  // from
  console.log(`: ${to.path}`)
})

export default router