<template>
  <component
    :is="tag"
    :type="tag === 'button' ? type : undefined"
    :href="tag === 'a' ? href : undefined"
    :to="tag === 'router-link' ? to : undefined"
    :disabled="disabled || loading"
    :class="buttonClasses"
    v-bind="$attrs"
    @click="handleClick"
  >
    <!--  -->
    <svg 
      v-if="loading" 
      class="animate-spin -ml-1 mr-2 h-4 w-4" 
      xmlns="http://www.w3.org/2000/svg" 
      fill="none" 
      viewBox="0 0 24 24"
    >
      <circle 
        class="opacity-25" 
        cx="12" 
        cy="12" 
        r="10" 
        stroke="currentColor" 
        stroke-width="4"
      />
      <path 
        class="opacity-75" 
        fill="currentColor" 
        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
      />
    </svg>

    <!--  -->
    <slot name="icon-left">
      <component 
        :is="iconLeft" 
        v-if="iconLeft && !loading" 
        :class="iconLeftClasses"
      />
    </slot>

    <!--  -->
    <span
      v-if="$slots.default || text"
      :class="textClasses"
    >
      <slot>{{ text }}</slot>
    </span>

    <!--  -->
    <slot name="icon-right">
      <component 
        :is="iconRight" 
        v-if="iconRight" 
        :class="iconRightClasses"
      />
    </slot>
  </component>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import type { Component } from 'vue'

type IconProp = string | Component

interface Props {
  variant?: 'primary' | 'secondary' | 'outline' | 'ghost' | 'danger'
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl'
  text?: string
  loading?: boolean
  disabled?: boolean
  fullWidth?: boolean
  rounded?: 'none' | 'sm' | 'md' | 'lg' | 'full'
  tag?: 'button' | 'a' | 'router-link'
  type?: 'button' | 'submit' | 'reset'
  href?: string
  to?: string
  iconLeft?: IconProp
  iconRight?: IconProp
}

const props = withDefaults(defineProps<Props>(), {
  variant: 'primary',
  size: 'md',
  text: '',
  loading: false,
  disabled: false,
  fullWidth: false,
  rounded: 'md',
  tag: 'button',
  type: 'button',
  href: undefined,
  to: undefined,
  iconLeft: undefined,
  iconRight: undefined
})

const emit = defineEmits<{
  click: [event: MouseEvent]
}>()

// 
const baseClasses = [
  'inline-flex',
  'items-center',
  'justify-center',
  'font-medium',
  'transition-all',
  'duration-200',
  'focus:outline-none',
  'focus:ring-2',
  'focus:ring-offset-2',
  'disabled:opacity-50',
  'disabled:cursor-not-allowed',
  'disabled:transform-none',
  'select-none'
]

// 
const sizeClasses = {
  xs: ['text-xs', 'px-2.5', 'py-1.5', 'gap-1'],
  sm: ['text-sm', 'px-3', 'py-2', 'gap-1.5'],
  md: ['text-sm', 'px-4', 'py-2', 'gap-2'],
  lg: ['text-base', 'px-6', 'py-3', 'gap-2.5'],
  xl: ['text-lg', 'px-8', 'py-4', 'gap-3']
}

// 
const roundedClasses = {
  none: ['rounded-none'],
  sm: ['rounded-sm'],
  md: ['rounded-lg'],
  lg: ['rounded-xl'],
  full: ['rounded-full']
}

// 
const variantClasses = {
  primary: [
    'bg-primary-600',
    'text-white',
    'hover:bg-primary-700',
    'active:bg-primary-800',
    'focus:ring-primary-500',
    'shadow-sm',
    'hover:shadow-md'
  ],
  secondary: [
    'bg-secondary-600',
    'text-white',
    'hover:bg-secondary-700',
    'active:bg-secondary-800',
    'focus:ring-secondary-500',
    'shadow-sm',
    'hover:shadow-md'
  ],
  outline: [
    'bg-transparent',
    'border-2',
    'border-primary-600',
    'text-primary-600',
    'hover:bg-primary-50',
    'active:bg-primary-100',
    'focus:ring-primary-500'
  ],
  ghost: [
    'bg-transparent',
    'text-primary-600',
    'hover:bg-primary-50',
    'active:bg-primary-100',
    'focus:ring-primary-500'
  ],
  danger: [
    'bg-red-600',
    'text-white',
    'hover:bg-red-700',
    'active:bg-red-800',
    'focus:ring-red-500',
    'shadow-sm',
    'hover:shadow-md'
  ]
}

// 
const widthClasses = computed(() => {
  return props.fullWidth ? ['w-full'] : []
})

// 
const buttonClasses = computed(() => {
  const classes = [
    ...baseClasses,
    ...sizeClasses[props.size],
    ...roundedClasses[props.rounded],
    ...variantClasses[props.variant],
    ...widthClasses.value
  ]

  // 
  if (props.loading) {
    classes.push('cursor-wait')
  }

  return classes
})

// 
const iconLeftClasses = computed(() => {
  const sizes = {
    xs: 'w-3 h-3',
    sm: 'w-4 h-4',
    md: 'w-4 h-4',
    lg: 'w-5 h-5',
    xl: 'w-6 h-6'
  }
  return sizes[props.size]
})

const iconRightClasses = computed(() => {
  const sizes = {
    xs: 'w-3 h-3',
    sm: 'w-4 h-4',
    md: 'w-4 h-4',
    lg: 'w-5 h-5',
    xl: 'w-6 h-6'
  }
  return sizes[props.size]
})

// 
const textClasses = computed(() => {
  const sizes = {
    xs: 'text-xs',
    sm: 'text-sm',
    md: 'text-sm',
    lg: 'text-base',
    xl: 'text-lg'
  }
  return sizes[props.size]
})

// 
function handleClick(event: MouseEvent) {
  if (props.disabled || props.loading) {
    event.preventDefault()
    return
  }
  emit('click', event)
}
</script>

<style scoped>
/* outline */
button:focus:not(:focus-visible),
a:focus:not(:focus-visible) {
  outline: none;
}

/*  */
:deep(.router-link-active) {
  opacity: 0.95;
}
</style>